import avatar from '../../../../components/yq-avatar/yq-avatar.vue';
export default {
	data() {
		return {
			form_data:{
				url: '../../../../static/login.png',
				name:'王某某',
				sex:'男',
				age:'18',
				addr:'杭州市萧山区',
				remark:'123456'
			},
			picker: ['男', '女'],
			sex_index:0,
			focus_obj:{
				name:false,
				age:false,
				addr:false,
				remark:false,
			},
			save_loading:false,
		};
	},
	components: {
		avatar
	},
	methods: {
		myUpload(rsp) {
			console.log(rsp)
			this.form_data.url = rsp.path; //更新头像方式一
			//rsp.avatar.imgSrc = rsp.path; //更新头像方式二
		},
		formSubmit(e) {
			console.log(e)
			if(this.save_loading){
				return
			}
			this.save_loading = true
			setTimeout(() =>{
				
				uni.showToast({
					title:'保存成功',
					icon: 'success',
					position: 'bottom',
					success: () => {
						this.save_loading = false
						
						// uni.hideToast();
						uni.navigateBack();
					}
				});
			},500)
		},
		PickerChange(e){
			this.sex_index = e.detail.value
			this.form_data.sex = this.picker[this.sex_index]
		},
		input_focus(key){
			console.log(key)
			this.focus_obj[key] = true
		},
		input_blur(key){
			this.focus_obj[key] = false
		}
	}
};
